@media only screen and (min-width: breakpoints(big-desktop)) {

  // Header with video
  .full-header {
    .chat-homescreen-with-video {
      position: fixed;
      top: -5px;

      .outer-container {
        padding: 0;
      }
    }

    .InfoBar {
      margin-top: 0.8rem;
    }
  }

  .small-header-with-video {
    .chat-routes-with-video {
      position: fixed;
      top: 30px;
    }
  }

  // Header without video
  .chat-routes-without-video {
    position: fixed;
    top: 13px;
  }
}

@media only screen and (max-width: breakpoints(tablet)) {
  .chat {
    display: none;
  }
}

