@media only screen and (max-width: breakpoints(phone)) {

  // Solar System
  .solar-system {
    position: fixed;
    z-index: -1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 40vh;
    height: 40vh;
    margin: 0 auto;
    box-shadow: inset 0 0 100px rgba(255, 255, 255, 0.1);
    border-radius: 70vw;
    display: flex;
    justify-content: center;
    align-self: center;
    opacity: 0.8;
  }

  // Header 
  .small-header-without-video,
  .message-controls-container {
    @include flex-column;
  }

  .small-header-without-video {
    height: $mobile-header-height;
    box-shadow: 0px 0px 15px 15px $etikett-black;

    nav {
      height: 60px;
      justify-content: flex-end;
    }

    .player-controls {
      display: flex;
      justify-content: flex-start;
      margin-left: 1.3rem;
      height: 40px;
      z-index: 0;
    }

    .message-controls-container {
      height: 75px;
      padding-top: 0;
      margin: 0 1rem;

    }

    .page-title-mobile {
      text-align: left;
      padding: 1rem;
    }

  }

  // Main Content
  .small-gap {
    margin-top: $mobile-header-height;
  }

  // Chat
  .chat {
    display: none;
  }

  .full-header .embeded-video {
    height: 50vh;
  }

  // Schedule
  .weekly-schedule:not(.this) {
    display: none;
  }

  // Archive
  .all-list {
    .archive-list-grid {
      @include flex-column;
      padding: 1rem;
    }

    .sort-by-box {
      flex-flow: row wrap;
      justify-content: center;
      width: 200px;
      margin: 0 auto;
    }

    .archive-card {
      img {
        width: 150px;
      }
    }
  }

  .archive-add-delete-buttons {
    margin: 0.5rem;
    justify-content: space-between;
  }

  // Blog
  .blog-content {
    width: 90%;
    margin: 0 auto;
  }

  .blog-date {
    font-size: 0.8rem;
  }

  .blog-text {
    font-size: 1rem;
  }

  // Forms
  .input-form {

    .grid-container {
      @include flex-column;
    }

    .grid-container,
    .submit-button {
      width: 90%;
      margin: 1rem auto;
    }
  }

  .edit-input-form {
    width: 90%;
  }

  // All Users
  .all-list .user-list-grid {
    @include flex-column;

    li {
      margin: 0;
    }
  }

  // All Hosts
  .all-list {

    .list-container {
      width: 80%;
      margin: 0 auto;
    }

    .host-list-grid {
      grid-template-columns: 50% 1fr 1fr;
    }

    li {
      &:nth-child(1) {
        justify-self: start;
      }

      &:nth-child(2) {
        justify-self: center;
      }
    }
  }

  .button-container {
    justify-content: end;
  }

  .mobile-menu {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
    padding: 0;
    top: 0;
    background-color: $etikett-black;
    left: 0;
    right: 0;

    padding-right: 0.5rem;


    &__btn {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 35px;
      height: 30px;
      cursor: pointer;
      transition: .4s;
    }

    &__icon {
      display: block;
      position: relative;
      background: rgba(255, 255, 255, 1);
      width: 90%;
      height: 4px;
      transition: .4s;

      &::after,
      &::before {
        content: "";
        display: block;
        position: absolute;
        background: rgba(255, 255, 255, 1);
        width: 100%;
        height: 4px;
        transition: .4s;
      }

      &::after {
        top: 8px;
      }

      &::before {
        top: -8px;
      }
    }


    &__container {
      position: fixed;
      top: 60px;
      left: 0;
      right: 0;
      height: 0;
      transition: .5s;
      transition-delay: .2s;
      overflow: hidden;
      background-color: $etikett-black;
      font-size: 1.4rem;

    }

    &__list {
      transition: .5s;
      transition-delay: .5s;
      list-style: none;
      padding-left: 0;
      display: flex;
      flex-direction: column;
    }

    &__item {
      font-size: 26px;
      padding-bottom: 15px;
      border-bottom: 1px solid rgba(66, 65, 65, 0.445);
      text-decoration: none;
    }

    &__link {
      text-decoration: none;
      color: #fff;
    }

    &__checkbox {
      display: none;

      &:checked~ {
        .mobile-menu {


          &__nav {
            opacity: 1;
            transition-delay: 0s;
          }

          &__container {
            height: 100%;
            transition-delay: 0s;
            z-index: 3;
            font-size: 1.4rem;
          }

          &__btn {
            .mobile-menu__icon {
              background: transparent;

              &::before,
              &::after {
                top: 0;
              }

              &::after {
                transform: rotate(-45deg);
                -webkit-transform: rotate(-45deg);
              }

              &::before {
                transform: rotate(45deg);
                -webkit-transform: rotate(45deg);
              }
            }
          }
        }
      }
    }
  }

  //host page

  .host-page {


    .carousel-item {
      @include flex-column;
      padding: 1rem;
    }

    .host-content {
      width: 300px;
      margin: 1rem;


      &:hover {
        box-shadow: 0 14px 28px rgba(139, 139, 139, 0.25),
          0 10px 10px rgba(156, 156, 156, 0.22);
        transition: all 0.5s cubic-bezier(.25, .8, .25, 1);
      }
    }

    .host-images {
      width: 100px;
      height: 100px;
      margin-left: 0.2rem;
    }


    .host-card {
      width: 300px;
      border-right: 1px solid rgba(190, 190, 195, 0.219);
      box-shadow: 0 -1px 20px rgba(255, 255, 255, 0.12);

    }

    .host-description {

      h3 {
        padding: 0 0 0.5rem 0.2rem;
      }

      .about-host {
        padding: 0;
      }


      .social-media-container {
        display: flex;
        justify-content: flex-end;
        padding: 1.2rem 0.5rem 0 0;

        i {
          margin-left: .5rem;
        }
      }

      .host-pagelink {
        display: flex;
        justify-content: flex-end;
        padding-right: 0.5rem;
      }
    }

    .side-list {
      display: none;
      // flex-direction: column;
      // justify-content: center;

      // border-left: 1px solid rgba(190, 190, 195, 0.219);
      // // box-shadow: 0 -1px 20px rgba(255, 255, 255, 0.12);
      // padding-left: 2px;

      // background-image: linear-gradient(to right, $etikett-black, $etikett-black-000, $etikett-black-000);

      // text-decoration-line: underline;
      // text-align: left;

      .side-list-item {
        margin: 0.5rem 0;

        .side-list-name {

          text-transform: lowercase;
          cursor: pointer;
          letter-spacing: 3px;
          transition: 0.1s;

          &.active {
            color: $etikett-hover;

          }

          &:hover {
            color: $etikett-hover;
            text-decoration-line: underline;
            text-decoration-color: $etikett-hover;
          }
        }
      }
    }
  }
}